/* #region header */
.header {
  box-sizing: border-box;

  height: 100vh;
  background-image: url(./image/Cover_photo.png);
  background-size: cover;
  background-position: 85% 15%;
  }

.header__title {
  margin: 0;
  font-family: Poppins, sans-serif;
  font-weight: 700;
  font-size: 36px;
  line-height: 100%;
  letter-spacing: -1.5px;
  text-align: center;
  vertical-align: middle;
  height: 36;
  top: 356px;
  left: 21px;
  color: #FFF;
  padding: 356px 20px 88px;
  }

  .header__bottom {
    padding: 0 20px 40px;
  }

