.top-bar {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding: 24px 20px 88px;

  @media (min-width: 576px) {
    padding: 36px 72px;
    }

  &__icon {
    display: flex;
    gap: 24px;
    }

    &__logo {
      display: flex;
    }
}

.menu {
  box-sizing: border-box;
  height: 100vh;
  background-color: #C2C2C2;
  font-weight: 600;
  text-transform: uppercase;

  &-top {
    margin-bottom: 24px;
  }

  &__nav {
    padding-bottom: 40px;
  }

  &__content {
    padding-inline: 20px;
  }

&__phone-number {
display: block;
margin-bottom: 16px;
color: inherit;
text-decoration: none;
font-size: 16px;
line-height: 27px;
}

&__book-a-test-ride {
display: block;
width: fit-content;

color: inherit;
text-decoration: none;
font-size: 12px;
line-height: 16px;
letter-spacing: 2px;
padding-bottom: 7px;
border-bottom: 1px solid #1D1D1D;
}
}

.icon {
  display: block;
  height: 24px;
  width: 24px;
  background-size: cover;

  @media (min-width: 576px) {
  height: 32px;
  width: 32px;
  }

  &--phone {
    background-image: url(./image/Icon-Phone-call.png);
    }

    &--menu {
    background-image: url(./image/Icon-Burger-menu.png);
    }

    &--close {
      background-image: url(./image/Buttons.png);
    }
  }
