
@import 'variables';
@import 'mixins';

@import 'move';
@import 'header';
@import 'top-bar';
@import 'navigation';
@import 'compare';
@import 'wrapper';
@import 'contact-us';


main {
  background-color: #1D1D1D;
}

.page {
  font-family: Poppins, sans-serif;
  font-size: 23px;
  line-height: 140%;
  background: #FFF;
  scroll-behavior: smooth;


  &__body {
    margin: 0;
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    opacity: 0;
    pointer-events: none;

    &:target {
      &:target {
    opacity: 1;
    pointer-events: all;
    }
    }
  }
}


